/// <reference lib="webworker" />

import { LapMissing } from '../model/lap-missing';

addEventListener('message', ({ data }) => {

  const lapMissing = data.lapMissing as LapMissing[];

  const lapMissingByRaceNumber = getLapMissingByRaceNumber(lapMissing);
  const lapMissingCount = lapMissing.length;

  postMessage({
    lapMissingByRaceNumber: lapMissingByRaceNumber,
    lapMissingCount: lapMissingCount
  });
});

function getLapMissingByRaceNumber(lapMissing: LapMissing[]): Map<number, number[]> {
  const result = new Map<number, number[]>();
  lapMissing.forEach(lap => {
    const raceNumber = lap.raceNumber;
    if (result.has(raceNumber)) {
      result.get(raceNumber)?.push(lap.lapNumber);
    } else {
      result.set(raceNumber, [lap.lapNumber]);
    }
  });
  return result;
}
